.tabs__container {
    position: relative;
}

.tabs {
    background-color: var(--fond);
    max-width: 1000px;
    padding: 10px;
    width: 100%;
}

.tabs .tabs__list {
    display: flex;
    margin: 0 0 10px;
    overflow-x: scroll;
}

.tabs .tabs__list .tabs__item {
    background: none;
    border: none;
    color: var(--text);
    cursor: pointer;
    font: 1.6rem/100% var(--font-mono);
    min-width: 150px;
    padding: 5px 10px 7px;
    position: relative;
    transition: all 0.5s ease;
}

.tabs .tabs__list .tabs__item:hover,
.tabs .tabs__list .tabs__item.active,
.tabs .tabs__list .tabs__item:focus {
    color: var(--special-text);
    font-weight: bold;
}

.tabs .tabs__list .tabs__item::before {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    height: 2px;
    background-color: var(--special-text);
    transform: scaleX(0);
    transform-origin: center bottom;
    transition: transform 0.5s ease;
}

.tabs .tabs__list .tabs__item.active::before {
    animation: active 0.5s ease-in-out forwards;
    transform: scaleX(1);
}

.tabs__panel {
    position: relative;
}

.tabs__panel .tabs__panel-item {
    display: none;
}

.tabs__panel .tabs__panel-item.active {
    animation: show-in 0.5s ease-in-out forwards;
    display: block;
    transform: scale(0.9);
    opacity: 0;
}

.tabs__panel .tabs__panel-item h3 {
    color: var(--text);
    font: 1.8rem/120% var(--font-mono);
}

.tabs__panel .tabs__panel-item h3 span {
    color: var(--light-text);
    font-weight: bold;
}

.tabs__panel .tabs__panel-item h3 a {
    color: var(--special-text);
    text-decoration: none;
    font-weight: bold;
}

.tabs__panel .tabs__panel-item p {
    color: var(--text);
    font: 1.6rem/100% var(--font-mono);
    margin: 10px 0;
}

.tabs__panel .tabs__panel-item ul {
    margin: 0;
    padding: 10px 20px;

    
}


.tabs__panel .tabs__panel-item ul li {
    font: 1.6rem/120% var(--font-mono);

    
    
}

@media (min-width: 800px) {
    .tabs__panel {
        padding: 0 ;
    }
}

.tabs .tabs__list::-webkit-scrollbar {
    height: 12px;
}

.tabs .tabs__list::-webkit-scrollbar-track {
    background-color: transparent;
}

.tabs .tabs__list::-webkit-scrollbar-thumb {
    background: none;
    background-color: #495670;
    border: 3px solid var(--fond);
    border-radius: 10px;
}

.tabs .tabs__list::-webkit-scrollbar-thumb:hover {
    background-color: #495670;
}

@keyframes show-in {
    0% {
        opacity: 0;
        transform: scale(0.9);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}